import React from 'react';
import _ from 'lodash';

import {Spinner, Combobox, Icon, RadioButtonGroup, Radio} from '@salesforce/design-system-react';

import {comboboxFilterAndLimit} from '../lib/utils';

const IS_MAPPED_FIELD = 'Is Mapped?'
const DATALAKE_FIELD = 'Datalake Field';
const SALESFORCE_FIELD = 'Salesforce Field';


const MAPPED_FIELDS = 'Mapped Fields';
const UNMAPPED_FIELDS = 'Un-mapped fields';
const ALL_FIELDS = 'All fields';


class GRAXRestoreMappingFilter extends React.Component {
	constructor(props) {
        super(props);

		this.state = {
            checked: ALL_FIELDS
        };
    }

    onChangeHandler(val) {
        console.log("onChangeHandler");
        console.log(val);
        this.setState({
            checked: val
        });

        if(this.props.onChange) {
            this.props.onChange(val);
        }
    }

    render() {
        const options = [MAPPED_FIELDS, UNMAPPED_FIELDS, ALL_FIELDS];

		return (
            <div className="slds-p-bottom_medium">
                <RadioButtonGroup
                    onChange={(event) => this.onChangeHandler(event.target.value)}

                    disabled={this.props.disabled}
                    required={this.props.required}
                    name={this.props.name}
                    errorId={this.props.errorId}
                >
                    {_.map(options, function(option, i) {
                        return (
                            <Radio
                                key={'option' + i}
                                id={option}
                                label={option}
                                value={option}
                                checked={this.state.checked === option}
                                variant="button-group"
                                deselectable={true}
                            />
                        );
                    }.bind(this))}
                </RadioButtonGroup>
            </div>
		);
	}
}


class GRAXRestoreMappingDataLakeField extends React.Component {
    constructor(props) {
		super(props);

		this.state = {
			inputValue: '',
            selection: [],
            isOpened: false
		};
    }

    componentDidMount() {
        if(this.props.selection) {
            this.setState({
                selection: this.props.selection
            })
        }
    }

    filterOptionsHandler() {
        var options = this.props.options;

        if(this.props.filterOptionsHandler && this.state.isOpened) {
            options = this.props.filterOptionsHandler(options);
        }

        return comboboxFilterAndLimit({
            inputValue: this.state.inputValue,
            limit: 1000,
            options: options,
            selection: this.state.selection
        })
    }

    render() {
        return (
            <React.Fragment>
                <Combobox
                    classNameMenu={'scrollable-GRAXRestoreMappingDataLakeField'}
                    events={{
                        onChange: (event, { value }) => {
                            this.setState({
                                inputValue: value
                            });

                            if (this.props.onChange) {
                                this.props.onChange(event, value);
                            }
                        },
                        onRequestRemoveSelectedOption: (event, data) => {
                            this.setState({
                                inputValue: '',
                                selection: data.selection,
                            });

                            if (this.props.onRequestRemoveSelectedOption) {
                                this.props.onRequestRemoveSelectedOption(event, data.selection);
                            }
                        },
                        onSelect: (event, data) => {
                            this.setState({
                                inputValue: '',
                                selection: data.selection,
                            });

                            if (this.props.onSelect) {
                                this.props.onSelect(event, data.selection);
                            }
                        },
                        onOpen: (event, data) => {
                            this.setState({
                                isOpened: true
                            });
                        },
                        onClose: (event, data) => {
                            this.setState({
                                isOpened: false
                            });
                        }
                    }}
                    labels={{
                        placeholder: 'Select DataLake Field',
                    }}
                    options={this.filterOptionsHandler()}
                    selection={this.state.selection}
                    value={this.state.inputValue}
                    variant="readonly"
                    hasStaticAlignment={true}
                    multiple={true}
                />
            </React.Fragment>
        );
    }
}


export default class GRAXRestoreMappingFields extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            _graxFieldOptions: null,
            _graxFieldOptionsList: null,
            _sfFieldOptions: null,
            _sfFieldOptionsList: null,
            _sfMappedFields: null,
            _error: null,

            _filterSelection: ALL_FIELDS,
            isPreviouslyMapped: false
        };
    }

    isLoaded() {
        return this.state._error || (this.state._graxFieldOptions != null && this.state._sfFieldOptions != null);
    }

    componentDidMount() {

        var _graxFieldOptionsList = _.map(this.props.graxFieldOptions, function(item, i) {
            return item.id;
        });

        /*var _sfFieldOptionsList = _.map(this.props.sfFieldOptions, function(item, i) {
            return item.id;
        });*/

        var _sfMappedFields = this.props.sfMappedFields || {};

        var _sfFieldOptions = this.props.sfFieldOptions;

        // Map selection
        _.forEach(_sfFieldOptions, (sfField, i) => {
            var mappedFields = _.reduce(_sfMappedFields[sfField.id] || [sfField.id], (_selection, graxFieldName) => {
                if (_graxFieldOptionsList.indexOf(graxFieldName) !== -1) {
                    _selection.push(graxFieldName);
                }

                return _selection;
            }, []);

            if(mappedFields.length > 0) {
                _sfMappedFields[sfField.id] = mappedFields;
            }
        });

        this.setState({
            _graxFieldOptions: this.props.graxFieldOptions,
            _graxFieldOptionsList: _graxFieldOptionsList,
            _sfFieldOptions: _sfFieldOptions,
            //_sfFieldOptionsList: _sfFieldOptionsList,
            _sfMappedFields: _sfMappedFields,
            sfMappedFieldsList: Object.keys(_sfMappedFields),
            graxMappedFieldsList: _.flatten(_.values(_sfMappedFields))
        });
    }

    shouldComponentUpdate(nextProps, nextState) {
        return (
            this.state._filterSelection !== nextState._filterSelection
            || this.props.objectName !== nextProps.objectName
            || this.state._graxFieldOptions !== nextState._graxFieldOptions
            || this.state._sfFieldOptions !== nextState._sfFieldOptions
            || this.props.graxFieldOptions !== nextProps.graxFieldOptions
            || this.props.sfFieldOptions !== nextProps.sfFieldOptions
            || this.props.sfMappedFields !== nextProps.sfMappedFields
            || this.props.renderKey !== nextProps.renderKey
        );
    }

    onMappingChangeHandler(fieldName, selection) {
        var mappedValues = _.map(selection, function(item, i) {
            return item.id;
        });

        var _sfMappedFields = this.state._sfMappedFields;

        if(selection) {
            _sfMappedFields[fieldName] = mappedValues;
        } else {
            delete _sfMappedFields[fieldName];
        }

        this.setState({
            _sfMappedFields: _sfMappedFields,
            sfMappedFieldsList: Object.keys(_sfMappedFields),
            graxMappedFieldsList: _.flatten(_.values(_sfMappedFields))
        });

        if(this.props.onMappingChange) {
            this.props.onMappingChange(fieldName, mappedValues);
        }
    }

    render() {
        if (this.state._error) {
            return <div>Error: {this.state._error}</div>;
        } else if (!this.isLoaded()) {
            return (
                <div style={{ position: 'relative', height: '5rem' }}>
                    <Spinner size="small" variant="base" />
                </div>
            );
        } else {
            var sfFieldOptions = this.state._sfFieldOptions;

            if(this.state._filterSelection === MAPPED_FIELDS) {
                sfFieldOptions = _.filter(sfFieldOptions, (item, i) => {
                    return (
                        this.state._sfMappedFields != null
                        && this.state.sfMappedFieldsList.indexOf(item.id) !== -1
                        && this.state._sfMappedFields[item.id].length !== 0
                    );
                });
            }

            if(this.state._filterSelection === UNMAPPED_FIELDS) {
                sfFieldOptions = _.filter(sfFieldOptions, (item, i) => {
                    return (
                        this.state.sfMappedFieldsList.indexOf(item.id) === -1
                        || this.state._sfMappedFields[item.id] == null
                        || this.state._sfMappedFields[item.id].length === 0
                    );
                });
            }

            sfFieldOptions = _.sortBy(sfFieldOptions, ['id']);

            return (
                <React.Fragment>
                    <GRAXRestoreMappingFilter onChange={(val) => this.setState({_filterSelection: val})} />

                    <div className="slds-scrollable_y" style={{maxHeight: '50vw'}}>
                        <table className="slds-table slds-table_cell-buffer slds-table_bordered slds-no-row-hover">
                            <thead>
                                <tr className="slds-line-height_reset">
                                    {this.state._filterSelection === ALL_FIELDS && (
                                        <th className="slds-text-title_caps" scope="col">
                                            <div className="slds-truncate" title={IS_MAPPED_FIELD}>{IS_MAPPED_FIELD}</div>
                                        </th>
                                    )}
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={DATALAKE_FIELD}>{DATALAKE_FIELD}</div>
                                    </th>
                                    <th className="slds-text-title_caps" scope="col">
                                        <div className="slds-truncate" title={SALESFORCE_FIELD}>{SALESFORCE_FIELD}</div>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                {/* Iterate over fields */}
                                {_.map(sfFieldOptions, function(sfField, i) {
                                    var fieldName = sfField.id;

                                    return (
                                        <tr className="slds-hint-parent" key={fieldName}>
                                            {this.state._filterSelection === ALL_FIELDS && (
                                                <td data-label={IS_MAPPED_FIELD} style={{width: '10vw'}}>
                                                    {this.state._sfMappedFields[fieldName] != null && this.state._sfMappedFields[fieldName].length > 0 && (
                                                        <Icon
                                                            category="utility"
                                                            color="warning"
                                                            name="check"
                                                            size="x-small"
                                                        />
                                                    )}
                                                </td>
                                            )}
                                            <td data-label={SALESFORCE_FIELD} style={{width: '60vw'}}>
                                                <GRAXRestoreMappingDataLakeField
                                                    options={this.state._graxFieldOptions}
                                                    filterOptionsHandler={(options) => {
                                                        return _.filter(options, (option, i) => {
                                                            return this.state.graxMappedFieldsList.indexOf(option.id) === -1 && sfField.type === option.type;
                                                        });
                                                    }}
                                                    selection={_.map(this.state._sfMappedFields[fieldName], (graxFieldName, i) => {
                                                        return {
                                                            id: graxFieldName,
                                                            label: graxFieldName
                                                        };
                                                    })}
                                                    onSelect={(event, selection) => this.onMappingChangeHandler(fieldName, selection)}
                                                    onRequestRemoveSelectedOption={(event, selection) => this.onMappingChangeHandler(fieldName, selection)}
                                                />
                                            </td>
                                            <td data-label={DATALAKE_FIELD} style={{width: '30vw'}}>
                                                <div className="slds-truncate" title={sfField.id}>{sfField.id}</div>
                                            </td>
                                        </tr>
                                    );
                                }.bind(this))}
                            </tbody>
                        </table>
                    </div>

                </React.Fragment>
            );
        }
    }
}