/* eslint-disable no-restricted-globals */
import React from 'react';
import _ from 'lodash';

import SLDSSection from './SLDSSection';
import GRAXSObjectCombobox from './GRAXSObjectCombobox';

import {Spinner, Button, Checkbox, Modal} from '@salesforce/design-system-react';

import {$, baseUrl, GetParams, pageParams} from '../lib/grax-data-lake';

import GRAXOrgCombobox from './GRAXOrgCombobox';
import GRAXRestoreMappingFields from './GRAXRestoreMappingFields';
import GRAXRestoreMappingDefaults from './GRAXRestoreMappingDefaults';

function normalizeFieldType(typeName) {
    var nft;

    switch (typeName) {
        case 'date':
        case 'datetime':
            nft = 'date';
            break;
        case 'boolean':
            nft = 'boolean';
            break;
        case 'double':
        case 'long':
        case 'percent':
        case 'currency':
            nft = 'number';
            break;
        default:
            nft = 'text';
            break;
    }

    return nft;
}


const HAS_CHANGES_MESSAGE = 'Some changes have not been saved, do you want to continue?';

const DEFAULT_VALUES_KEY = '__defaultValues__';

export default class GRAXRestoreMapping extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            selectedSObject: null,
            _graxFieldOptions: null,
            _sfFieldOptions: null,
            _sfMappedFields: {},
            _sfDefaultValues: {}, // to null
            _error: null,
            isLoading: true,
            _isCustomOrg: false,
            _customOrg: null,
            customMapping: null,
            hasChanges: false,
            isSaving: false,
            resetConfirmIsOpen: false,
            resetConfirmIsChecked: false,
            resetConfirmCallback: () => ({}),
            renderCounter: 0
        }
    }

    _onMappingChangeHandler(fieldName, mappedValues) {
        console.log(fieldName, mappedValues);

        var _sfMappedFields = this.state._sfMappedFields;

        _sfMappedFields[fieldName] = mappedValues;

        this.setState({
            _sfMappedFields: _sfMappedFields,
            hasChanges: true
        });
    }

    _onOrgSelectionChangeHandler(selection) {
        console.log(selection);

        var _customOrg = selection[0] || null;

        if(this.state._customOrg !== _customOrg && (!this.state.hasChanges || confirm(HAS_CHANGES_MESSAGE))) {
            this.setState({
                _customOrg: _customOrg,
            });
            // this.state._customOrg = _customOrg;

            this.onMappingSettingsChange(this.state.selectedSObject, this.state.customMapping);
        }
    }

    _onOrgSelectionRemoveHandler(selection) {
        var _customOrg = selection[0] || null;

        if(this.state._customOrg !== _customOrg && (!this.state.hasChanges || confirm(HAS_CHANGES_MESSAGE))) {
            this.setState({
                _customOrg: _customOrg,
                _sfMappedFields: {},
                _sfDefaultValues: {}
            });
        }
    }

    isMapLoaded() {
        return this.state._error || (
            this.state.customMapping != null
            && this.state._graxFieldOptions != null
            && this.state._sfFieldOptions != null
            && this.state._sfDefaultValues != null
        );
    }

    componentDidMount() {
        this.setState({
            customMapping: this.props.customMapping
        });
    }

    onMappingSettingsChange(selectedSObject, data) {
        var customMapping = this.state.customMapping[selectedSObject] || {};

        if(this.state._isCustomOrg && this.state._customOrg) {
            customMapping = customMapping[this.state._customOrg] || {};
        }

        var fieldMap = customMapping.fieldMap || {};

        this.setState({
            _sfFieldOptions: _.map(data, (item, i) => ({
                id: item.name,
                label: item.name,
                type: normalizeFieldType(item.type),
                sfType: item.type
            })),
            _sfMappedFields: _.omit(fieldMap, [DEFAULT_VALUES_KEY]) || {},
            _sfDefaultValues: fieldMap[DEFAULT_VALUES_KEY] || {}
        });
    }

    sObjectChangeHandler(selectedSObject) {
        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/salesforce/sobjects/' + selectedSObject + '/fields' + GetParams({action:'load'}),
            data: {
                _bools: 'createable',
                _select: 'name,type'
            },
            success: function(data) {
                this.onMappingSettingsChange(selectedSObject, data);
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    _error: err.toString()
                });
            }.bind(this)
        });

        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/getDataTypeFields/grax' + selectedSObject.toLowerCase() + 'index' + GetParams({action:'load'}),
            data: {},
            success: function(data) {
                this.setState({
                    _graxFieldOptions: _.map(data, (val, key) => ({
                        id: key,
                        label: key,
                        type: normalizeFieldType(val)
                    }))
                });
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    _error: err.toString()
                });
            }.bind(this)
        });
    }

    getCustomMappingToSave() {
        console.log('this.state.customMapping', this.state.customMapping);

        var _sfMappedFields = this.state._sfMappedFields;

        var customMapping = {
            fieldMap: _.reduce(_sfMappedFields, (customMapping, mappedFields, fieldName) => {
                if(!(_.isArray(mappedFields) && mappedFields.length === 1 && mappedFields[0] === fieldName)) {
                    customMapping[fieldName] = mappedFields;
                }

                return customMapping;
            }, {})
        };

        if(!_.isEmpty(this.state._sfDefaultValues)) {
            customMapping.fieldMap[DEFAULT_VALUES_KEY] = this.state._sfDefaultValues;
        }

        if(this.state._isCustomOrg && this.state._customOrg) {
            customMapping = _.assign({}, this.state.customMapping[this.state.selectedSObject.id] || {}, {
                [this.state._customOrg]: customMapping
            });
        }

        console.log('customMapping', customMapping);

        return _.assign({}, this.state.customMapping, {
            [this.state.selectedSObject.id]: customMapping
        });
    }

    saveCustomMapping(customMappingToSave) {
        console.log('customMappingToSave', customMappingToSave);

        this.setState({
            isSaving: true
        });

        $.ajax({
            type: 'POST',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/saveCustomMapping' + GetParams({action:'save'}),
            contentType: "application/json; charset=utf-8",
            data: JSON.stringify(customMappingToSave),
            success: function(data) {
                console.log(data);

                this.setState({
                    customMapping: customMappingToSave,
                    hasChanges: false,
                    isSaving: false
                });

                if(this.props.onMappingChange) {
                    this.props.onMappingChange(customMappingToSave);
                }
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    _error: err.toString(),
                    isSaving: false
                });
            }.bind(this)
        });

        //alert('Save in progress...');
    }

    render() {
        return (
            // <div style={{position: 'relative'}}>
            <div>
                {this.state.isSaving && <Spinner size="large" variant="brand" />}

                <SLDSSection isCollapsible={true} isOpen={true} title="Select your Salesforce Object">
                    {this.state._error || <GRAXSObjectCombobox
                        menuItem={(props) => {
                            if(this.state.customMapping[props.option.id]) {
                                return <b>{props.option.id}</b>;
                            }

                            return props.option.id;
                        }}
                        onSelect={(event, data) => {
                            var selectedSObject = data[0];

                            this.setState({
                                selectedSObject: selectedSObject
                            });

                            this.sObjectChangeHandler(selectedSObject.id);
                        }}
                        onRequestRemoveSelectedOption={(event, data) => {
                            if(!this.state.hasChanges || confirm(HAS_CHANGES_MESSAGE)) {
                                this.setState({
                                    selectedSObject: null,
                                    _graxFieldOptions: null,
                                    _sfFieldOptions: null,
                                    //_sfDefaultValues: null,
                                    hasChanges: false
                                });

                                return true;
                            }

                            return false;
                        }}
                        onLoad={(success, err) => {
                            this.setState({
                                isLoading: false,
                                _error: err ? err.toString : null
                            })
                        }}
                        selectionKeys={pageParams.datalakeIndex != null ? [pageParams.datalakeIndex] : null}
                    />}

                    {!this.state.isLoading && <Checkbox
                        className="slds-p-top_medium"
                        labels={{
                            label: 'Would you like to define a custom field Mapping for a source Organization?',
                        }}
                        onChange={(e) => {
                            e.persist();

                            this.setState({
                                _isCustomOrg: e.target.checked
                            })
                        }}
                    />}

                    {this.state._isCustomOrg && (
                        <div style={{maxWidth: '50vw'}}>
                            <GRAXOrgCombobox
                                onSelect={(event, selection) => this._onOrgSelectionChangeHandler(selection)}
                                onRequestRemoveSelectedOption={(event, selection) => this._onOrgSelectionRemoveHandler(selection)}
                            />
                        </div>
                    )}

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="Map the fields to the appropriate Salesforce field">
                    {
                        this.state.selectedSObject && (this.isMapLoaded() ? (
                            <GRAXRestoreMappingFields
                                key={'GRAXRestoreMappingFields_' + this.state.renderCounter}
                                renderKey={'GRAXRestoreMappingFields_' + this.state.renderCounter}
                                objectName={this.state.selectedSObject.id}
                                graxFieldOptions={this.state._graxFieldOptions}
                                sfFieldOptions={this.state._sfFieldOptions}
                                sfMappedFields={this.state._sfMappedFields}
                                onMappingChange={this._onMappingChangeHandler.bind(this)}
                            />
                        ) : (
                            <div style={{ position: 'relative', height: '5rem' }}>
                                <Spinner size="medium" variant="brand" />
                            </div>
                        ))
                    }

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <SLDSSection isCollapsible={true} isOpen={true} title="Set a default field value for fields on this object">
                {
                        this.state.selectedSObject && (this.isMapLoaded() ? (
                            <GRAXRestoreMappingDefaults
                                key={'GRAXRestoreMappingDefaults_' + this.state.renderCounter}
                                renderKey={'GRAXRestoreMappingDefaults_' + this.state.renderCounter}
                                objectName={this.state.selectedSObject.id}
                                sfFieldOptions={this.state._sfFieldOptions}
                                sfDefaultValues={this.state._sfDefaultValues}
                                onDefaultValueChangeHandler={(_sfDefaultValues) => {
                                    console.log(_sfDefaultValues);

                                    this.setState({
                                        _sfDefaultValues: _sfDefaultValues,
                                        hasChanges: true
                                    });
                                }}
                            />
                        ) : (
                            <div style={{ position: 'relative', height: '5rem' }}>
                                <Spinner size="medium" variant="brand" />
                            </div>
                        ))
                    }

                    <div className="slds-m-bottom_large"></div>
                </SLDSSection>

                <div style={{
                  "margin-bottom": "25rem"
                }}>
                    {this.state.selectedSObject && this.isMapLoaded() && (
                        <React.Fragment>
                            <Button label="Save" variant="brand" onClick={() => {
                                var customMappingToSave = this.getCustomMappingToSave();

                                this.saveCustomMapping(customMappingToSave);
                            }}/>

                            {this.state.customMapping && this.state.customMapping[this.state.selectedSObject.id] && (
                                <Button label="Reset Mapping" variant="destructive" onClick={() => {
                                    this.setState({
                                        resetConfirmIsOpen: true,
                                        resetConfirmIsChecked: false,
                                        resetConfirmCallback: (() => {
                                            if(this.state.resetConfirmIsChecked && confirm('Are you sure that you want to reset the selected mapping?')) {
                                                this.setState({
                                                    _sfMappedFields: {},
                                                    _sfDefaultValues: {},

                                                    renderCounter: this.state.renderCounter + 1,

                                                    resetConfirmIsOpen: false,
                                                    resetConfirmCallback: (() => {}),
                                                });

                                                var customMappingToSave = this.getCustomMappingToSave();

                                                if(customMappingToSave[this.state.selectedSObject.id]) {
                                                    delete customMappingToSave[this.state.selectedSObject.id];
                                                }

                                                this.saveCustomMapping(customMappingToSave);
                                            }
                                        })
                                    });
                                }} />
                            )}
                        </React.Fragment>
                    )}
                </div>

                {this.state.selectedSObject && this.isMapLoaded() && (
                    <Modal
                        dismissible={true}
                        footer={[
                            <div key="resetCheck" className="slds-p-bottom_small">
                                <Checkbox
                                    assistiveText={{
                                        label: 'I understand the risks',
                                    }}
                                    id="reset-confirm"
                                    labels={{
                                        label: 'I understand the risks',
                                    }}
                                    onChange={(e) => {
                                        e.persist();

                                        this.setState({
                                            resetConfirmIsChecked: e.target.checked
                                        });
                                    }}
                                />
                            </div>,
                            <Button
                                key="cancelBtn"
                                label="Cancel"
                                onClick={() => {
                                    this.setState({
                                        resetConfirmIsOpen: false,
                                        resetConfirmIsChecked: false
                                    });
                                }}
                            />,
                            <Button
                                key="resetBtn"
                                label="Reset"
                                onClick={this.state.resetConfirmCallback}
                                variant="destructive"
                                disabled={!this.state.resetConfirmIsChecked}
                            />,
                        ]}
                        isOpen={this.state.resetConfirmIsOpen}
                        onRequestClose={() => {
                            this.setState({
                                resetConfirmIsOpen: false,
                                resetConfirmIsChecked: false
                            });
                        }}
                        prompt="error"
                        size="medium"
                        title={<span>Reset <b>{this.state.selectedSObject.id}</b> mapping?</span>}
                    >
                        <div className="slds-m-around_medium">
                            Removing the mapping will set the mapping back to the default values.
                            <br />
                            Once a mapping has been reset, it cannot be restored.
                        </div>
                    </Modal>
                )}
            </div>
        );
    }
}