import React from 'react';
import _ from 'lodash';

import {Spinner, Combobox} from '@salesforce/design-system-react';

import {$, baseUrl, GetParams} from '../lib/grax-data-lake';

import {comboboxFilterAndLimit} from '../lib/utils';


export default class GRAXSObjectCombobox extends React.Component {
    constructor(props) {
		super(props);

		this.state = {
            options: [],
			inputValue: '',
            selection: [],
            isLoaded: false,
            error: null
		};
    }
    
    componentDidMount() {
        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/salesforce/describeGlobal/sobjects' + GetParams({action:'load'}),
            data: {
                _bools: 'createable,layoutable,triggerable',
                _select: 'name,label'
            },
            success: function(data) {
                this.setState({
                    options: _.map(data, (item, i) => ({
                        id: item.name,
                        label: item.label,
                        index: 'grax' + item.name.toLowerCase() + 'index'
                    })),
                    isLoaded: true
                });

                if(this.props.selectionKeys != null) {
                    this.setState({
                        selection: _.filter(this.state.options, (item) => this.props.selectionKeys.indexOf(item.index) !== -1 || this.props.selectionKeys.indexOf(item.id) !== -1)
                    });
                }

                if(this.props.onLoad) {
                    this.props.onLoad(true);
                }

                if(this.props.onSelect && this.state.selection != null && this.state.selection.length > 0) {
                    this.props.onSelect(null, this.state.selection);
                }
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    isLoaded: true,
                    error: err.toString()
                });

                if(this.props.onLoad) {
                    this.props.onLoad(false, err);
                }
            }.bind(this)
        });
    }

    render() {
        if (this.state.error) {
            return <div>Error: {this.state.error}</div>;
        } else if (!this.state.isLoaded) {
            return (
                <div style={{ position: 'relative', height: '2rem' }}>
                    <Spinner size="small" variant="base" />
                </div>
            );
        } else {
            return (
                <React.Fragment>
                    <Combobox
                        classNameMenu={'scrollable-GRAXSObjectCombobox'}
                        events={{
                            onChange: (event, { value }) => {
                                this.setState({
                                    inputValue: value
                                });

                                if (this.props.onChange) {
                                    this.props.onChange(event, value);
                                }
                            },
                            onRequestRemoveSelectedOption: (event, data) => {
                                if (this.props.onRequestRemoveSelectedOption && this.props.onRequestRemoveSelectedOption(event, data.selection)) {
                                    this.setState({
                                        inputValue: '',
                                        selection: data.selection,
                                    });

                                    return true;
                                }

                                return false;
                            },
                            onSelect: (event, data) => {
                                this.setState({
                                    inputValue: '',
                                    selection: data.selection,
                                });

                                if (this.props.onSelect) {
                                    this.props.onSelect(event, data.selection);
                                }
                            },
                        }}
                        labels={{
                            placeholder: 'Select Salesforce Object',
                        }}
                        options={comboboxFilterAndLimit({
                            inputValue: this.state.inputValue,
                            limit: 1000,
                            options: this.state.options,
                            selection: this.state.selection
                        })}
                        selection={this.state.selection}
                        value={this.state.inputValue}
                        variant="inline-listbox"
                        required={true}
                        menuItem={this.props.menuItem}
                    />
                </React.Fragment>
            );
        }
    }
}